"use client";
import { useState, useEffect } from "react";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import {
  Tabs,
  TabsContent,
  TabsList,
  TabsTrigger,
} from "@/components/ui/tabs";
import {
  Card,
  CardContent,
} from "@/components/ui/card";
import * as XLSX from "xlsx";
import jsPDF from "jspdf";
import autoTable from "jspdf-autotable";

interface LogItem {
  nama: string;
  kelas: string;
  aktivitas: string;
  lokasi: string;
  tanggal: string;
  foto?: string;
}

interface LaporanItem {
  pelapor: string;
  lokasi: string;
  deskripsi: string;
  tanggal: string;
}

interface AgendaItem {
  judul: string;
  tanggal: string;
  keterangan: string;
  lokasi?: string;
}

interface GaleriItem {
  lokasi: string;
  jenis: string;
  bentuk: string;
  waktu: string;
  foto?: string;
  video?: string;
}

const getTanggalSekarang = (): string => {
  const hariList = ["Minggu", "Senin", "Selasa", "Rabu", "Kamis", "Jumat", "Sabtu"];
  const bulanList = ["Januari", "Februari", "Maret", "April", "Mei", "Juni", "Juli", "Agustus", "September", "Oktober", "November", "Desember"];
  const now = new Date();
  return `${hariList[now.getDay()]}, ${now.getDate()} ${bulanList[now.getMonth()]} ${now.getFullYear()}`;
};

const exportToExcel = (data: any[], filename: string) => {
  const worksheet = XLSX.utils.json_to_sheet(data);
  const workbook = XLSX.utils.book_new();
  XLSX.utils.book_append_sheet(workbook, worksheet, "Sheet1");
  XLSX.writeFile(workbook, `${filename}.xlsx`);
};

const exportToPDF = (columns: string[], data: any[], filename: string) => {
  const doc = new jsPDF();
  autoTable(doc, {
    head: [columns],
    body: data.map(Object.values),
  });
  doc.save(`${filename}.pdf`);
};

export default function GercabGo() {
  const [nama, setNama] = useState("");
  const [kelas, setKelas] = useState("");
  const [aktivitas, setAktivitas] = useState("");
  const [lokasi, setLokasi] = useState("");
  const [foto, setFoto] = useState<string | undefined>();
  const [video, setVideo] = useState<string | undefined>();
  const [logList, setLogList] = useState<LogItem[]>([]);
  const [pelapor, setPelapor] = useState("");
  const [laporLokasi, setLaporLokasi] = useState("");
  const [deskripsi, setDeskripsi] = useState("");
  const [laporanList, setLaporanList] = useState<LaporanItem[]>([]);
  const [judulAgenda, setJudulAgenda] = useState("");
  const [tanggalAgenda, setTanggalAgenda] = useState("");
  const [lokasiAgenda, setLokasiAgenda] = useState("");
  const [ketAgenda, setKetAgenda] = useState("");
  const [agendaList, setAgendaList] = useState<AgendaItem[]>([]);
  const [jenisBudaya, setJenisBudaya] = useState("");
  const [bentukBudaya, setBentukBudaya] = useState("");
  const [lokasiBudaya, setLokasiBudaya] = useState("");
  const [waktuBudaya, setWaktuBudaya] = useState("");
  const [galeriList, setGaleriList] = useState<GaleriItem[]>([]);

  useEffect(() => {
    const stored = localStorage.getItem("gercab_data");
    if (stored) setLogList(JSON.parse(stored));
    const storedLaporan = localStorage.getItem("gercab_laporan");
    if (storedLaporan) setLaporanList(JSON.parse(storedLaporan));
    const storedAgenda = localStorage.getItem("gercab_agenda");
    if (storedAgenda) setAgendaList(JSON.parse(storedAgenda));
    const storedGaleri = localStorage.getItem("gercab_galeri");
    if (storedGaleri) setGaleriList(JSON.parse(storedGaleri));
  }, []);

  useEffect(() => {
    localStorage.setItem("gercab_data", JSON.stringify(logList));
  }, [logList]);
  useEffect(() => {
    localStorage.setItem("gercab_laporan", JSON.stringify(laporanList));
  }, [laporanList]);
  useEffect(() => {
    localStorage.setItem("gercab_agenda", JSON.stringify(agendaList));
  }, [agendaList]);
  useEffect(() => {
    localStorage.setItem("gercab_galeri", JSON.stringify(galeriList));
  }, [galeriList]);

  const exportLog = () => {
    exportToExcel(logList, "Jejak_Hijau");
    exportToPDF(["Nama", "Kelas", "Aktivitas", "Lokasi", "Tanggal"], logList, "Jejak_Hijau");
  };

  const exportLaporan = () => {
    exportToExcel(laporanList, "Laporan");
    exportToPDF(["Pelapor", "Lokasi", "Deskripsi", "Tanggal"], laporanList, "Laporan");
  };

  const exportAgenda = () => {
    exportToExcel(agendaList, "Agenda");
    exportToPDF(["Judul", "Tanggal", "Keterangan", "Lokasi"], agendaList, "Agenda");
  };

  const exportGaleri = () => {
    const dataToExport = galeriList.map(({ foto, video, ...rest }) => rest);
    exportToExcel(dataToExport, "Galeri");
    exportToPDF(["Lokasi", "Jenis", "Bentuk", "Waktu"], dataToExport, "Galeri");
  };

  return (
    <div className="p-6 space-y-4">
      <h1 className="text-2xl font-bold text-green-700">Gercab-GO: Ekspor Data</h1>
      <Tabs defaultValue="jejak">
        <TabsList>
          <TabsTrigger value="jejak">Jejak</TabsTrigger>
          <TabsTrigger value="lapor">Laporan</TabsTrigger>
          <TabsTrigger value="agenda">Agenda</TabsTrigger>
          <TabsTrigger value="galeri">Galeri</TabsTrigger>
        </TabsList>
        <TabsContent value="jejak">
          <Button className="mr-2 bg-green-600 text-white" onClick={exportLog}>Export Jejak</Button>
        </TabsContent>
        <TabsContent value="lapor">
          <Button className="mr-2 bg-red-600 text-white" onClick={exportLaporan}>Export Laporan</Button>
        </TabsContent>
        <TabsContent value="agenda">
          <Button className="mr-2 bg-blue-600 text-white" onClick={exportAgenda}>Export Agenda</Button>
        </TabsContent>
        <TabsContent value="galeri">
          <Button className="mr-2 bg-yellow-600 text-white" onClick={exportGaleri}>Export Galeri</Button>
        </TabsContent>
      </Tabs>
    </div>
  );
}
